<template>
  <div>
    <form>
      <div class="titleWrap">
        <h2></h2>
      </div>
      <div class="inputWrap">
        <p></p>
        <el-input
          placeholder=" ."
          v-model="email"
          clearable
        >
        </el-input>
      </div>
      <div class="inputWrap">
        <p></p>
        <el-input
          placeholder=" ."
          v-model="password"
          show-password
        ></el-input>
      </div>
      <div class="btnWrap">
        <el-button @click="login()"></el-button>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      email: '',
      password: '',
    }
  },
  methods: {
    async login() {
      await this.$store.dispatch('auth/login', {
        email: this.email,
        password: this.password,
      })
    },
  },
}
</script>
