<template>
  <div class="signupWrap">
    <form>
      <!--   -->
      <div v-if="!result">
        <div class="titleWrap">
          <h2></h2>
        </div>
        <div class="inputWrap">
          <p></p>
          <el-input
            placeholder="."
            v-model="name"
            clearable
          >
          </el-input>
        </div>
        <div class="inputWrap">
          <p></p>
          <el-input
            placeholder=" ."
            v-model="email"
          ></el-input>
        </div>
        <div class="inputWrap">
          <p></p>
          <el-input
            placeholder=" ."
            v-model="password"
            show-password
          ></el-input>
        </div>
        <div class="inputWrap">
          <p> </p>
          <el-upload
            action="#"
            :on-change="uploadFile"
            class="upload-demo"
            name="attachment"
            :auto-upload="false"
          >
            <el-button slot="trigger" size="small" type="primary"
              >select file</el-button
            >
            <div class="el-upload__tip" slot="tip"
              >jpg/png files with a size less than 500kb</div
            >
          </el-upload>
        </div>
        <div class="btnWarp">
          <el-button @click="signUp()"></el-button>
        </div>
      </div>
      <!--   -->
      <div v-else>
        <el-result
          icon="success"
          title=" "
          :subTitle="`${name}  .`"
        >
          <template slot="extra">
            <el-button @click="$router.push({ name: 'login' })"
              ></el-button
            >
          </template>
        </el-result>
      </div>
    </form>
  </div>
</template>
<script>
import { signUp } from '@/apis/auth'

export default {
  data() {
    return {
      name: '',
      password: '',
      email: '',
      file: null,
      result: false,
    }
  },
  methods: {
    signUp() {
      this.$store.dispatch('common/getLoading', true)
      const formData = new FormData()
      // body  formData 
      if (this.file) {
        formData.append('attachment', this.file.raw, this.file.name)
      }
      formData.append('name', this.name)
      formData.append('email', this.email)
      formData.append('password', this.password)
      // signUp({
      //   name: this.name,
      //   email: this.email,
      //   password: this.password,
      // })
      signUp(formData)
        .then(({ data }) => {
          this.$message({
            showClose: true,
            message: `${data.name}  .`,
            type: 'success',
          })
          this.result = true
        })
        .catch((err) => {
          this.$message({
            showClose: true,
            message: err.response.data.errors.message,
            type: 'error',
          })
        })
        .finally(() => {
          this.$store.dispatch('common/getLoading', false)
        })
    },
    uploadFile(file) {
      this.file = file
    },
  },
}
</script>
